import { IApi, IRecordType, IPrintResponse } from "../IApi";

class Api implements IApi {
    async postPrintForms(recordTypes: string[]): Promise<IPrintResponse> {
        const x = await fetch("/api/recordtype/printforms", {
            method: "POST",
            headers: { 'Content-Type':'application/json'},
            body: JSON.stringify(recordTypes)
        });
        const x_1 = await x.json();
        return x_1;
    }
    async getRecordType(): Promise<IRecordType[]> {
        const x = await fetch("/api/recordtype/recordtypes");
       
        const x_1 = await x.json();
        console.log(x_1);
        return x_1;
    }
}

export default Api;


