import { ModuleOf } from "./ModuleOf";
import { IApi, IRecordType, IPrintResponse } from "./IApi";

declare var api: ModuleOf<IApi>;

class ApiController implements IApi
{
    postPrintForms(recordTypes: string[]): Promise<IPrintResponse> {
        return api.default.prototype.postPrintForms(recordTypes);
    }
    getRecordType(): Promise<IRecordType[]> {
        return api.default.prototype.getRecordType();
    }
}

export default ApiController;


