import * as React from 'react';
import { IRecordType, IApi } from '../api/IApi';
import { Button, WithStyles, Theme, createStyles, withStyles, CssBaseline, Grid, Typography, Paper, ListItem, Checkbox, ListItemText, List, AppBar, Toolbar } from '@material-ui/core';


const styles = (theme: Theme) => createStyles({
    root: {
        display: "flex",
        justifyContent: "center",
        marginTop: 16
    },
    grid: {
        width: "80%"
    },
    paper: {
        padding: 6
    },
    button: {
        display: "flex",
        justifyContent: "flex-end"
    },
    container: {
        display:"flex",
        flexDirection: "column"
    },
    appBarRoot: {
        zIndex: 1
    }
});

interface IProps extends WithStyles<typeof styles>
{
    apiController: IApi;
}

interface IState
{
    recordTypes: IViewRecordType[];
}

interface IViewRecordType
{
    recordType: string;
    checked: boolean
}

class App extends React.Component<IProps, IState> {
    private readonly _apiController: IApi;
    private readonly FORM_FILE_NAME = 'TmBoardingForms';

    constructor(props: IProps)
    {
        super(props);
        this.state = {
            recordTypes: []
        }

        this._apiController = props.apiController;
    }

    async componentWillMount()
    {

        const response = await this._apiController.getRecordType();
        const recordTypes = response.map((x: IRecordType) =>
        {
            return { recordType: x.name, checked: false } as IViewRecordType;
        });
        this.setState({ recordTypes });
    }

    onClick = async () =>
    {
        const { recordTypes } = this.state;
        const filter = recordTypes.filter(x => x.checked).map((x: IViewRecordType) =>
        {
            return x.recordType;
        });
        const data = await this._apiController.postPrintForms(filter);
        this.generatePdf(data.data, this.FORM_FILE_NAME);

    }

    generatePdf = (data: string, fileName: string) =>
    {
        var byteCharacters = atob(data);
        var byteNumbers = new Array(byteCharacters.length);
        for (let i = 0; i < byteCharacters.length; i++)
        {
            byteNumbers[i] = byteCharacters.charCodeAt(i);
        }
        var byteArray = new Uint8Array(byteNumbers);
        var blob = new Blob([byteArray], { type: 'application/pdf' });

        var objectUrl = URL.createObjectURL(blob);

        if (window.navigator.msSaveBlob)
        {
            window.navigator.msSaveOrOpenBlob(blob, fileName);
        } else
        {
            window.open(objectUrl, "_blank");
        }
        setTimeout(() =>
        {
            URL.revokeObjectURL(objectUrl);
        }, 100);

        return true;
    }

    onCheck = (e: string) =>
    {
        console.log('click', e);
        const { recordTypes } = this.state;
        const found = recordTypes.find(x => x.recordType === e) as IViewRecordType;
        found.checked = !found.checked;
        this.setState({ recordTypes });

    }

    render()
    {
        const { classes } = this.props;
        return (
            <div className={classes.container}>
                <CssBaseline />
                <AppBar classes={{root: classes.appBarRoot}} position="relative" color="primary">
                    <Toolbar>
                        <Typography variant="h6" color="inherit">Print Blank Forms</Typography>
                    </Toolbar>
                </AppBar>
                <div className={classes.root}>
                    <Grid container className={classes.grid}>
                        <Grid item xs={12}>
                            <Paper className={classes.paper} elevation={2}>
                                <List dense>
                                    {this.state.recordTypes.map((x: IViewRecordType, i: number) =>
                                    {
                                        return <ListItem key={i} role={undefined} dense disableGutters button onClick={() => this.onCheck(x.recordType)}>
                                            <Checkbox
                                                checked={x.checked}
                                                tabIndex={-1}
                                                disableRipple
                                            />
                                            <ListItemText primary={x.recordType} />
                                        </ListItem>
                                    })}
                                </List>
                                <div className={classes.button}>
                                    <Button onClick={this.onClick} variant="contained" size="small" color="primary" >Print Selected Forms</Button>
                                </div>
                            </Paper>
                        </Grid>
                    </Grid>
                </div>
            </div>
        );
    }
}




export default withStyles(styles)(App);;


